body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background: rgb(111, 128, 167);
}

.container-main-login {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  width: 100vw;
}

.login-title {
  -webkit-background-clip: text;
          background-clip: text;
  background-image: url('https://i.pinimg.com/originals/62/39/4d/62394d753859943e6a1a36443ef78795.gif');
  background-size: contain;
  color: transparent;
  font-family: Sigmar One , cursive;
  font-size: 50px;
  line-height: 0.75;
  text-align: center;
  -webkit-text-stroke: 2px white;
}

.login-panel {
  align-items: center;
  display: flex;
  flex-flow: column;
  justify-content: center;
}

.label-login-content p {
  align-self: flex-start;
  display: flex;
  margin-left: 12px;
}

.login-panel label {
  align-items: center;
  display: flex;
  flex-flow: row;
  justify-content: center;
}

.config-button-login2 {
  display: flex;
  text-decoration: none;
  margin-top: 1.5em;
}

.config-button-login {
  display: flex;
  text-decoration: none;
}

.login-panel input {
  border-radius: 8px;
  padding: 20px;
  margin: auto;
  width: 200px;
  outline: none;
}

.login-panel input {
  margin: 10px;
  padding: 5px;
}

.login-panel .btn-login-screen {
  background-color: darkslategrey;
  border: none;
  border-radius: 5px;
  box-shadow: 3px 3px 3px black;
  color: white;
  padding: 10px 24px;
  width: 100%;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.login-panel .btn-login-screen:hover {
  background-color: aquamarine;
  color: black;
  cursor: pointer;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

@media only screen and (max-width: 450px) {
  .login-title {
    font-size: 160%;
  }

  .login-panel input {
    width: 4em;
  }

  .input-login-size {
    width: 60% !important;
  }
}

@media only screen and (max-width: 280px) {
  .login-title {
    font-size: 1em;
  }

  .login-panel input {
    width: 4em;
  }

  .input-login-size {
    width: 40% !important;
  }
}

.header-main-container {
  margin: auto;
  width: 100%;
}

.header {
  align-items: center;
  background-color: #3a3b3c;
  box-shadow: 3px 3px 6px black;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  opacity: 0.75;
  padding: 10px;
  margin: auto;
  position: fixed;
  top: 0;
  -webkit-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
  width: 100%;
}

.header-logos {
  align-items: center;
}

.header-infos {
  align-items: center;
  justify-content:center;
  flex-wrap: wrap;
  display: flex;
}

.header:hover {
  opacity: 1;
}

.header-wings {
  width: 40px;
}

.header-game-title {
  -webkit-background-clip: text;
          background-clip: text;
  background-image: url('https://i.pinimg.com/originals/62/39/4d/62394d753859943e6a1a36443ef78795.gif');
  background-size: contain;
  color: transparent;
  font-family: Sigmar One , cursive;
  font-size: 40px;
  line-height: 0.75;
  text-align: center;
  -webkit-text-stroke: 2px white;
}

.header-img {
  border-radius: 50%;
  box-shadow: 1px 1px 8px black;
  -webkit-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
}

.header-text {
  color: #e4e6eb;
  font-family: Alfa Slab One , cursive;
  font-size: 24px;
  margin: 0 20px;
  text-shadow: 3px 3px 3px black;
  -webkit-text-stroke: 1px black;
  -webkit-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
}

@media only screen and (max-width: 1020px) {
  .header-wings {
    width: 30px;
  }

  .header-text {
    font-size: 20px;
  }

  .header-game-title {
    font-size: 30px;
  }

  .header-img {
    width: 40px;
  }
}

@media only screen and (max-width: 800px) {
  .header-wings {
    width: 0px;
  }

  .header-text {
    font-size: 20px;
  }

  .header-game-title {
    font-size: 0px;
  }

  .header-img {
    width: 40px;
  }
}

.loading {
  color: whitesmoke;
  font-family: Gochi Hand , cursive;
  font-size: 80px;
  font-weight: 700;
  margin: 250px auto;
  text-align: center;
  text-shadow: 3px 3px 3px black;
  -webkit-text-stroke: 1px black;
}

.game-content {
  align-items: center;
  background-color: #444;
  border-radius: 10px;
  box-shadow: 0 0 10px 2px black;
  flex-direction: column;
  display: flex;
  justify-content: center;
  max-height: 800px;
  max-width: 700px;
  margin-top: 12em;
  padding: 2% 2%;
}

.bat {
  margin: 40px 0;
  width: 24%;
}

.question {
  color: whitesmoke;
  font-family: 'Courier New', Courier, monospace;
  letter-spacing: 2px;
  font-size: 18px;
}

.game-content .buttons {
  box-shadow: none;
  display: inline-block;
  height: auto;
  margin: 1px;
  padding: 1px;
  text-align: center;
  width: auto;
}

.game-content button {
  background-color: #2069e0;
  border-radius: 5px;
  box-shadow: 3px 3px 3px black;
  font-size: 18px;
  font-weight: 700;
  height: auto;
  margin-left: 10px;
  margin-top: 10px;
  padding: 10px 40px;
  white-space: nowrap;
  width: auto;
}

.game-content button:hover {
  background-color: aquamarine;
  color: black;
  cursor: pointer;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.game-content button:disabled {
  background-color: whitesmoke;
  -webkit-transform: unset;
          transform: unset;
}

.correct {
  border: 3px solid rgb(6, 240, 15);
}

.incorrect {
  border: 3px solid red;
}

.next-question {
  background-color: darkslategrey;
  border: none;
  border-radius: 5px;
  box-shadow: 3px 3px 3px black;
  color: white;
  padding: 10px 24px;
  width: 100%;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

button .feedback {
  align-items: center;
  border-radius: 50%;
  color: white;
  display: inline-flex;
  flex-direction: column;
  font-size: 18px;
  font-weight: 700;
  height: 120px;
  justify-content: center;
  margin: 20px;
  padding: 10px;
  text-align: center;
  white-space: normal;
  width: 120px;
}

@media only screen and (max-width: 800px) {
  .question {
    font-size: 16px;
  }

  .game-content button {
    font-size: 15px;
  }
}

@media only screen and (max-width: 400px) {
  .game-content button {
    background-color: #2069e0;
    border-radius: 5px;
    box-shadow: 3px 3px 3px black;
    font-size: 14px;
    font-weight: 700;
    height: auto;
    margin-left: 6px;
    margin-top: 6px;
    padding: 4px 18px;
  }

  .game-content {
    align-items: center;
    background-color: #444;
    border-radius: 10px;
    box-shadow: 0 0 10px 2px black;
    flex-direction: column;
    display: flex;
    justify-content: center;
    max-height: 800px;
    max-width: 700px;
    margin-top: 24em;
    padding: 2% 2%;
  }
}


.timer {
  color: #e4e6eb;
  font-family: Alfa Slab One , cursive;
  font-size: 24px;
  text-shadow: 3px 3px 3px black;
  -webkit-text-stroke: 1px black;
  -webkit-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
}

.game-screen-content {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 73vh;
  justify-content: center;
}

@media only screen and (max-width: 1020px) {
  .timer {
    font-size: 20px;
  }
}

.feedback-content {
  align-items: center;
  background-color: #444;
  border-radius: 10px;
  box-shadow: 4px 4px 50px black;
  display: flex;
  flex-direction: column;
  height: auto;
  margin: 1px auto;
  margin-top: 180px;
  max-height: 700px;
  max-width: 500px;
  padding: 20px 40px;
  width: auto;
}

.feedback-text, .feedback-score {
  color: aliceblue;
  font-family: 'Press Start 2p' , cursive;
  font-size: 32px;
  text-shadow: 3px 3px black;
  text-align: center;
}

.feedback-text:hover, .feedback-score:hover {
  -webkit-animation: movein 0.3s infinite;
          animation: movein 0.3s infinite;
}

@-webkit-keyframes movein {

  0% { color: #444; }

  100% { color: whitesmoke; }
}

@keyframes movein {

  0% { color: #444; }

  100% { color: whitesmoke; }
}

.feedback-content button {
  background-color: #2069e0;
  border-radius: 5px;
  box-shadow: 3px 3px 3px black;
  font-size: 18px;
  font-weight: 700;
  height: auto;
  margin-left: 10px;
  margin-top: 10px;
  padding: 10px 40px;
  white-space: nowrap;
  width: auto;
}

.fireworks {
  margin: 1em;
  width: 180px;
}

.feedback-content button:hover {
  background-color: aquamarine;
  color: black;
  cursor: pointer;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

@media only screen and (max-width: 700px) {
  .timer {
    font-size: 20px;
  }

  .feedback-text, .feedback-score {
    color: aliceblue;
    font-family: 'Press Start 2p' , cursive;
    font-size: 20px;
    text-shadow: 3px 3px black;
  }

  .fireworks {
    width: 120px;
  }
}

@media only screen and (max-width: 400px) {
  .feedback-content button {
    background-color: #2069e0;
    border-radius: 5px;
    box-shadow: 3px 3px 3px black;
    font-size: 14px;
    font-weight: 700;
    height: auto;
    margin-left: 6px;
    margin-top: 6px;
    padding: 4px 18px;
  }
}

.ranking-content {
  background-color: #444;
  border-radius: 10px;
  box-shadow: 3px 3px 3px black;
  height: auto;
  margin: 80px auto;
  max-height: 600px;
  max-width: 800px;
  padding: 20px;
  width: auto;
}

.ranking-content-inside {
  height: auto;
  max-height: 600px;
  max-width: 800px;
  overflow: auto;
  width: auto;
}

.ranking-content-inside h1 {
  -webkit-animation: movein 0.3s;
          animation: movein 0.3s;
  -webkit-animation-iteration-count: 3;
          animation-iteration-count: 3;
  color: whitesmoke;
  font-family: 'Press Start 2p' , cursive;
  font-size: 40px;
  text-align: center;
  text-shadow: 3px 3px 3px black;
}

@-webkit-keyframes movein {

  0% { color: green; }

  100% { color: yellow; }

}

@keyframes movein {

  0% { color: green; }

  100% { color: yellow; }

}

.ranking-content img {
  border-radius: 50%;
  box-shadow: 0 0  8px 3px black;
  width: 80px;
}

.ranking-content-items {
  background-color: rgb(206, 206, 206);
  border: 1px solid black;
  border-radius: 20px;
  box-shadow: 3px 3px 3px black;
  display: flex;
  justify-content: space-around;
  margin-left: 40px;
  margin-right: 50px;
  margin-top: 20px;
  padding: 10px;
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  width: 600px;
}

.ranking-content-items:hover {
  background-color: white;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.ranking-content-items span {
  font-family: 'Press Start 2p' , cursive;
  font-size: 22px;
  font-weight: 700;
  line-height: 80px;
}

.div-button {
  align-items: center;
  display: flex;
  flex-direction: column;
}

.div-button button {
  background-color: whitesmoke;
  border-radius: 5px;
  box-shadow: 3px 3px 3px black;
  font-size: 18px;
  font-weight: 700;
  height: auto;
  margin-left: 10px;
  margin-top: 10px;
  padding: 10px 40px;
  white-space: nowrap;
  width: auto;
}

.div-button button:hover {
  background-color: #fad47c;
  cursor: pointer;
  -webkit-transform: scale(1.2, 1.2);
          transform: scale(1.2, 1.2);
}

